* {
    margin: 0;
    padding: 0;
}

body {
    display: flex;
    justify-content: center;
    background-color: antiquewhite;
}


.player,
select {
    display: block;
    width: 250px;
    height: 40px;
    font-size: 1.5rem;
    color: rgb(70, 11, 9);
    background-color: antiquewhite;
    text-align: center;
    letter-spacing: 2px;
    border: none;
    border-bottom: 2px solid saddlebrown;
    outline: none;
    margin: 3px;
}

.config {
    margin-top: 4rem;
    text-align: start;
}

select {
    font-size: 1.2rem;
    margin: 1.5rem 0;
}

.label {
    color: rgb(13, 34, 34);
    background-color: whitesmoke;
    padding: .5rem;
    font-size: 1.1rem;
}


input:placeholder-shown {
    font-size: 1em;
}

.app {
    text-align: center;
}

.app h1 {
    font-size: 3rem;
    color: brown;
    margin: 2rem;
}

.toggleParent {
    margin-top: 2rem;
    display: flex;
    justify-content: space-between;
}

input[type='checkbox'] {
    -webkit-appearance: none;
    width: 60px;
    height: 30px;
    background: rgb(136, 131, 131);
    border-radius: 15px;
    outline: none;
    box-shadow: inset 0 0 5px rgba(0, 0, 0, .8);
}

input:checked[type='checkbox'] {
    background-color: brown;
}

input[type='checkbox']::before {
    content: '';
    position: relative;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    top: 0;
    left: 0;
    background: rgb(8, 5, 1);
}